import Section from '../Section/Section';
import s from './Products.module.css';

import Itemcard from './Itemcard';

const Products = ({ data }) => {
  console.log(data);
  return (
    <Section>
      <div className={s.Products__catalog}>
        {data.map((item, index) => {
          return (
            <Itemcard
              img={item.image}
              title={item.title}
              description={item.description}
              price={item.price}
              key={item.id}
            />
          );
        })}
      </div>
    </Section>
  );
};

export default Products;
